.cart-products-main{
    flex-grow: 1;
    .cart-product{
        display: flex;
        align-items: center;
        position: relative;
        border-bottom: 1px solid rgba(0, 0, 0,.1);
        &:hover{
            cursor: pointer;
            background-color: #e1dddd;
        }
        .product-img{
            width: 60px;
            height: 50px;

            img{
                width: 100%;
                height: 100%;
            }
        }
        .product-details{
            padding: 0 20px;
            width: 250px;
            
            
            .name{
                font-size: 16px;
                font-weight: 500;
                white-space: nowrap;
                margin: 5px 0;
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
                display: block;
                
                
            }
            .cross-icon{
                position: absolute;
                top: 15px;
                right: 20px;
                svg{
                    font-size: 17px;
                    transition: .5s;
                    cursor: pointer;
                    &:hover{
                        color: #8e2de2;
                    }
                }
            }

            .quantity-btn{
                width: fit-content;
                display: flex;
                height: 30px;
                border: 2px solid rgba(0, 0, 0,.3);
                margin-right: 10px;

                span {
                    font-size: 14px;
                    width: 30px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    color: #6b6b6b;
                    font-size: 17px;

                    &:nth-child(1){
                        border-right: 2px solid rgba(0, 0, 0,.3);
                        
                    }
                    &:nth-child(2){
                        border-right: 2px solid rgba(0, 0, 0,.3);
                        
                    }
                    &:nth-child(1){
                        // border-left: 2px solid rgba(0, 0, 0,.3);
                        
                    }
                 
                }

            }
            .small-text{
                display: flex;
                align-items: center;
                margin: 5px 0;
                span{
                    font-size: 14px;
                    color:  #8e2de2;
                }
            }
        }
    }
}