@import "../../css-config/mixins.scss";


.products-main{
    padding: 20px 0px;
    @include md{
        padding: 40px 0;
    }
    .products-heading{
        font-size: 20px;
        font-weight: 600;
        &:after{
            content: "";
            width: 40px;
            height: 3px;
            background-color: red;
            display: block;
            margin-top: 5px;
        }
    }
    .products-container{
        display: flex;
        flex-flow: wrap;
        gap: 10px;
        padding: 30px 0;
        @include md{
            gap: 20px;
        }

    }

}