@import "../../css-config/mixins.scss";


.cart-main{
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    display: flex;
    justify-content: flex-end;
    .opac-layer{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: rgba(0,0,0,.5);
        
    }
    .cart-container{
        width: 100%;
        height: 100%;
        background-color: white;
        z-index: 5;
        display: flex;
        flex-direction: column;
        position: relative;
        transform: translateX(100%);
        animation: cartAnimation .3s ease forwards;
        overflow: scroll;
        @include md{
            width: 360px;
        }

        .cart-header{
            display: flex;
            justify-content: space-between;
            padding: 20px 15px;
            border-bottom: 1px solid rgba(0,0,0,.1);


            span{
                flex: 22px;
                font-weight: 700;

            }
            .close-div{
                display: flex;
                align-items: center;
                gap: 5px;
                span{
                    font-size: 16px;
                    cursor: pointer;
                }
                svg{
                    font-size: 16px;
                    cursor: pointer;
                }

                &:hover{
                    opacity: .5;
                }
            }
        }

        .empty-cart{
            display: flex;
            flex-direction: column;
            margin-top: 100px;
            align-items: center;
            svg{
                font-size: 125px;
                margin-bottom: 20px;
                color: rgba(0,0,0,.1);
            }
            span{
                text-transform: capitalize;
                font-size: 15px;
                margin-bottom: 20px;
            }
            .return-cta{
                width: 120px;
                font-size: 16px;
                background-color: #8e2de2;
                height: 50px;
                color: white;
                cursor: pointer;
            }
        }

        .cart-footer{
            border-top: 1px solid rgba(0,0,0,.1);
            // position: absolute;
            // bottom: 0;
            // width: 100%;

            

            .subtotal{

                border-bottom: 1px solid rgba(0,0,0,.1);
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 20px;
                padding: 20px 15px;
                .text{
                    font-size: 22px;
                    font-weight: 600;
                }
                .total{
                    color: #8e2de2;
                }
            }
            .checkout-div{
                padding: 0px 15px 10px 15px;
                border-bottom: 1px solid rgba(0,0,0,.1);


                .checkout-cta{
                    width: 100%;
                    font-size: 16px;
                    display: flex;
                    flex-grow: 1;
                    background-color: #8e2de2;
                    height: 50px;
                    color: white;
                    cursor: pointer;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }
        }
    }
}

@keyframes cartAnimation{
    0%{transform: translateX(100%);}
    100%{transform: translateX(0);}

}