@import "../../css-config/mixins.scss";

.single-product-main-container{
    padding: 20px 0;
    @include md{
        padding: 40px 0;
    };
    .layout{
        width: calc(100% - 20px);
        margin: 0 auto;
        @include md{
            width: 1200px;
        }
    }
    .product-container{
        display: flex;
        flex-flow: wrap;
        gap: 2%;
        @include md{
            flex-direction: row;
            justify-content: space-between;
        }
        .left{
            width: 100%;
            background-color: rgba(0,0,0,.1);
            margin: 20px 0;

            @include md{
                width: 49%;
            }
            img{
                display: block;
                width: 100%;
            }
        }

        .right{
            width: 100%;
            display: flex;
            flex-direction: column;
            margin: 20px 0;

            @include md{
                width: 49%;
            }

            .name{
                font-size: 20px;
                font-weight: 600;
                margin-bottom: 20px;
                line-height: 1.3;
                @include md{
                    font-size: 24px;

                }
            }
            .price{
                font-size: 20px;
                font-weight: 600;
                margin-bottom: 20px;
                line-height: 15px;
                color: rgba(0, 0, 0, 0.7);
                @include md{
                    font-size: 22px;

                }
            }
            .desc{
                font-size: 15px;
                font-weight: 500;
                margin-bottom: 20px;
                line-height: 1.3;
                color: rgba(0, 0, 0, 0.5);
                @include md{
                    font-size: 16px;

                } 
            }
            .cart-btns{
                display: flex;
                margin-bottom: 30px;
                
                

                .quantity-btn{
                    width: fit-content;
                    display: flex;
                    height: 50px;
                    border: 2px solid rgba(0, 0, 0,.3);
                    margin-right: 10px;

                    span {
                        font-size: 20px;
                        width: 40px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        cursor: pointer;
                        color: #6b6b6b;

                        &:nth-child(1){
                            border-right: 2px solid rgba(0, 0, 0,.3);
                            
                        }
                        &:nth-child(2){
                            border-right: 2px solid rgba(0, 0, 0,.3);
                            
                        }
                        &:nth-child(3){
                            // border-left: 2px solid rgba(0, 0, 0,.3);
                            
                        }
                     
                    }

                }
                .add-to-cart-btn{
                    width: 120px;
                    font-size: 16px;
                    background-color: #8e2de2;
                    color: white;
                    cursor: pointer;

                    
                }

            }
            .divider{
                width: 100%;
                border: 1px solid rgba(0, 0, 0,.3);
                margin-bottom: 20px;
                
            }

            .bold{
                font-size: 16px;
                margin-bottom: 20px;

                span{
                    font-weight: bolder;
                    color: black;
                }
                p{
                    color: #6b6b6b;
                }

                .icons{
                    margin-left: 10px;
                    color: #6b6b6b;
                    cursor: pointer;

                    &:hover{
                        color: #8e2de2;
                    }
                }

                &.my-icons-div{
                    display: flex;

                    
                }
            }
        }
    }
}