@import "../../../css-config/mixins.scss";

.category-container{
    margin: 20px 0;
    @include md{
        margin:40px 0;
    }
    .categories{
        display: flex;
        flex-flow: wrap;
        gap: 10px;

        .category{
            width: calc(50% - 5px) ;
            overflow: hidden;
            background-color: black;

            @include md{
            width: calc(25% - 10px) ;

            }
            img{
                display: block;
                width: 100%;
                transition: .3s;
                &:hover{
                    cursor: pointer;
                    transform: scale(1.1);
                }
            }

        }
    }
}