@import "../../../css-config/mixins.scss";

.hero-banner {
 
  padding: 40px 0;
  position: relative;
  background-image: linear-gradient(to right, #ff5f6d, #ffc371);

  @include md {
    height: calc(100vh - 80px);
    
  }
  .content {
    height: 100%;
    display: flex;
    flex-direction: column-reverse;
    justify-content: flex-end;
    align-items: center;
    margin: 0 auto;
    max-width: calc(100% - 20px);
    position: relative;
    @include md {
      width: 1200px;
      flex-direction: row;
    }
    .banner-img {
      position: relative;
      z-index: 10;
      width: 200px;
      margin-bottom: 20px;

      @include md {
        width: 450px;
        height: 600px;
        margin-top: 50px;
        margin-right: 60px;
        margin-bottom: 0;
      }
      @include xxl {
        width: 600px;
        margin-top: 0px;
        margin-right: 0px;
      }
    }
    .text-content {
      color: white;
      text-align: center;
      display: flex;
      flex-direction: column;
      align-items: center;



      @include md {
        position: absolute;
        left: 150px;
        top: 50%;
        transform: translateY(-50%);
        width: 500px;

      }
      @include xxl {
        left: 0px;
      }
      h1 {
        line-height: 1;
        font-size: 80px;
        margin-bottom: 20px;
        font-weight: 700;
        @include md {
          font-size: 180px;
        }
      }
      p {
        min-width: 300px;
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 20px;

        @include md {
          min-width: 500px;
          font-size: 18px;
          line-height: 24px;
          margin-bottom: 40px;
        }
      }

      .ctas{
        display: flex;
        gap: 10px;
        .banner-cta{
            padding: 10px 20px;
            font-size: 15px;
            font-weight: 600;
            color: white;
            background-color: transparent;
            border: 2px solid #fff;
            cursor: pointer;
            text-transform: uppercase;
            &.v2{
                background-color: white;
                color: black;
                
            }
            &:hover{
                opacity: .6;
            }
        }
        
        
      }
    }
  }
}
