@import "../../css-config/mixins.scss";

.category-main{
    padding: 20px 0;
    width: 100%;
    @include md{
        padding: 20px 0;
    }
    .category-layout{
        width: calc(100% - 20px);
        margin:0 auto;
        @include md{
            width: 1200px;
        }
    }
    .category-heading{
        margin-top: 10px;


        span{
            font-size: 20px;
            font-weight: 600;

        }
    }
    .products-container{
        margin: 0px 0;
        @include md{
            margin: -30px 0;
        }
    }
}