@import "../../../css-config/mixins.scss";

.product-card{
    width: 48.5%;
    cursor: pointer;
        @include md{
            width: 23.5%;
        }
    
    .img-thumbnail{
        background-color: rgba(0,0,0,.1);
        overflow: hidden;
        

        

        img{
            width: 100%;
            transition: all ease .3s;

            &:hover{
                transform: scale(1.1);
            }
        }
    }

    .text-content{
        padding: 5px 5px;
        .p-name{
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
            

        }
        .p-price{
            font-size: 15px;
          
            
        }
    }
}
