@import "../../css-config/mixins.scss";

.footer-main{
    width: 100%;

    .footer-content{

    
        padding: 40px 10px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        @include md{
            padding: 40px 60px;
            justify-content: space-between;
        }
        .col{
            max-width: 180px;
            margin-bottom: 20px;
            .title{
                font-size: 20px;
                color: rgba(0,0,0);
                margin-bottom: 20px;
            }
            .text{
                font-size: 14px;
                color: rgba(0,0,0,.5);
                // margin-bottom: 20px;
                
            }

            .icon-text-div{
                display: flex;
                align-items: center;
                margin-bottom: 10px;
                .icon-div{


                    svg{
                        font-size: 14px;
                        color: rgba(0,0,0,.6);
                        margin-right: 10px;
                       
        
                        }
                }

            
            }
            span{
                font-size: 14px;
                color: rgba(0,0,0,.6);
                margin-bottom: 10px;
                display: block;
                cursor: pointer;
            }
            @include md{
                max-width: 300px;
        }
        }


        
        
        

    }
    .bottom-footer{
        width: 100%;
        border-top: 1px solid rgba(0,0,0,.5); 
        .bottom-content{
            display: flex;
            flex-direction: column;
            align-items: center;
            height: 70px;
            padding: 10px 0;
            @include md{
                flex-direction: row;
                justify-content: space-between;
                padding: 10px 50px;
            }
            .bottom-text{
                font-size: 14px;
                margin-bottom: 10px ;
                color: rgba(0,0,0,.5);
                
            }
        }       
    }
}