@import "../../../css-config/mixins.scss";



.main-newsletter{
    width: 100%;
    height: 400px;
    background: #f9f9f9 url("../../../assets/newsletter-bg.jpeg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    align-items: center;

    .newsletter-content{
        width: fit-content;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0 auto;
        
        .small-text{
            font-size: 19px;
            font-weight: 600;
            color: rgba(0,0,0,.4);
            margin-bottom: 15px;
        }
        .large-text{
            font-size: 24px;
            font-weight: 600;
            color: rgb(0,0,0);
            margin-bottom: 15px;
        }
        form{
            
            display: flex;
            gap: 8px;
        

            input{
                width: 200px;
                font-size: 18px;
                padding: 0 15px;

                @include md{
                    width: 500px;
                }

            }
            button{
                padding: 10px 20px;
                font-size: 15px;
                cursor: pointer;

            }
        }
        .sm-text{
            font-size: 14px;
            font-weight: 600;
            color: rgba(0,0,0,.4);
            margin-top: 15px;
        }
        .social-icons{
            display: flex;
            gap: 15px;
            margin-top: 15px;

            .icon{
            background-color: rgb(9, 9, 9);
            border-radius: 50%;
            color: #f9f9f9;
            cursor: pointer;
            padding: 5px;
            display: flex;
            align-items: center;
            justify-content: center;

            }
            
        }
    }
}