@import "../../../css-config/mixins.scss";

.search-main{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: white;
    z-index: 99;
    transform: translateY(100%);
    animation: showSearch .6s ease forwards;
    flex-grow: 1;

    .search-field{
        display: flex;
        justify-content: center;
        border-bottom: 1px solid rgba(0,0,0,.1);
        position: relative;
  

        input{
            outline:none;
            font-size: 24px;
            font-weight: 800;
            width: 100%;
            padding: 20px 40px;
            text-align: center;
            border: none;

            
        }
        .icon{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 10px;
            svg{
                font-size: 35px;
                cursor: pointer;
            }

        }
    }
    .show-search-products-main{
        width: 100%;
        height: 100%;
        position: relative;
        overflow: scroll;
        
        .s-products-content{
            width: calc(100% - 20px);
            margin: 0 auto;
            height: calc(100% - 70px);
            // height: 1100px;
            background-color: rgba(0,0,0,.1);
            // background-color: red;
            overflow: scroll;

            @include md{
                width: 800px;
            }

            .procuct-card{
                display: flex;
                align-items: center;
                gap: 0;
                padding: 10px 0px;
                background-color: rgb(248, 243, 243);

                &:hover{
                    background-color: rgb(236, 231, 231);

                    cursor: pointer;
                }

                .product-img{
                    height:  50px;
                    width: 25%;
                    // background-color: white;

                    @include md{
                    height: 80px;
                    width: 80px;
                    }
                    

                    img{
                        width: 100%;
                        height: 100%;
                    }
                }
                .product-details{
                    display: flex;
                    flex-direction: column;
                    overflow: hidden;
                    padding: 10px 0px;
                    width: 90%;

                    

                    .name{
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        display: block;
                        margin-bottom: 10px;
                        width: 100%;
                    }
                    .desc{
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        display: block;
                    }
                }
            }
        }

    }

}


@keyframes showSearch{
    0%{transform: translateY(100%);}
    100%{transform: translateY(0);}

}