@import "../../css-config/mixins.scss";


.header-container{
    width: 100%;
    background-color: #212121;
    color: white;
    padding: 0 20px;
    z-index: 99;
    border-bottom: 1px solid rgba(0,0,0,0.1);

    @include md{
        padding: 0 40px;
    }
    .header-content{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        max-width: 1200px;
        margin: 0 auto;
        @include md{
        height: 80px;
        }
        .left{
            list-style: none;
            display: none;
            gap: 25px;
            text-transform: uppercase;
            cursor: pointer;
            @include md{
                display: flex;
            }

            li{
                font-size: 14px;
                font-weight: 600;
               

            }
        }
        .center{
            font-size: 22px;
            font-weight: 700;
            cursor: pointer;
            @include md{
                font-size: 30px;
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                // margin-left: 50%;
            }
        }
        .right{
            display: flex;
            align-items: center; 
            gap: 20px;
            @include md{
                gap: 25px;
            }

            svg{
                font-size: 20px;
                cursor: pointer;
                @include md{
                    font-size: 24px;
                }
            }
            .cart-icon{
                position: relative;
                span{
                    max-width: 20px;
                    // text-align: center;
                    position: absolute;
                    top: -5px;
                    right: -7px;
                    padding: 3px 6px;
                    background-color: #8e2de2;
                    font-size: 10px;
                    line-height: 1;
                    border-radius: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    cursor: pointer;
                    @include md{
                        font-size: 13px;
                    }
                }
            }

        }
    }
    &.sticky-header{
        position: sticky;
        top: 0;
        transform: translateY(-80px);
        animation: mysticky-header .4s ease forwards;
    }
}

@keyframes mysticky-header{
    0%{transform: translateY(-80px);}
    100%{transform: translateY(0px);}

}